/** @section Google Map */
.google-map-markers{
  display: none;
}

.google-map-container {
  width: 100%;
}

.google-map {
  height: 250px;
  width: 100%;
  color: $body-color;

  @include media-breakpoint-up(sm) {
    height: 400px;
  }

  @include media-breakpoint-up(lg) {
    height: 520px;
  }
}
